import React, { Component, Suspense } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import { Container } from 'reactstrap';
import axios from 'axios';

import SchoolConfirm from './SchoolConfirm'
import SchoolMultiple from './SchoolMultiple'

import {
  AppAside,
  AppFooter,
  AppHeader,
  AppSidebar,
  AppSidebarFooter,
  AppSidebarForm,
  AppSidebarHeader,
  AppSidebarMinimizer,
  AppSidebarNav2 as AppSidebarNav,
} from '@coreui/react';

import * as router from 'react-router-dom';

class SchoolSelect extends Component {

  constructor(props){
    super(props)

    this.state = {
      status: "loading",
      schools: []
    }
    this.getSchools = this.getSchools.bind(this)
    this.selectSchool = this.selectSchool.bind(this)
    this.noConfirm = this.noConfirm.bind(this)
  }


  componentDidMount(){
    this.getSchools();
  }


  selectSchool(id){
    this.setState({status: "loading"})
    axios.post(process.env.REACT_APP_API_URL + '/user/schoolSelect', JSON.stringify({id: id}), { headers: { Authorization: `Bearer ${localStorage.getItem("access_token")}`, "Content-Type": 'application/json'} })
          .then(res => {
            const result = res.data;
            if(result.success){
              this.props.refresh();
            }
    })
  }

  noConfirm(){
    this.setState({status: "unsupported"})
  }

  getSchools(){
    axios.get(process.env.REACT_APP_API_URL + '/user/schoolSelect', { headers: { Authorization: `Bearer ${localStorage.getItem("access_token")}` } })
          .then(res => {
            const result = res.data;
            if(result.success){
              if(result.schools.length == 0){
                this.setState({status: "unsupported"})
              }else if(result.schools.length == 1){
                if(result.schools[0].unique_domain){
                  this.selectSchool(result.schools[0]._id)
                }else{
                  this.setState({status: "confirm", schools: result.schools})
                }
              }else{
                this.setState({status: "multiple", schools: result.schools})
              }
            }
    })
  }



  loading = () => <div className="animated fadeIn pt-1 text-center">Ladataan...</div>

  render(){
    return (
      <div className="app">
        <AppHeader fixed>
          <Suspense  fallback={this.loading()}>
          </Suspense>
        </AppHeader>
        <div className="app-body">
          <main className="main">
            <Container fluid>
              <Suspense fallback={this.loading()}>
                {this.state.status == "loading" &&
                  this.loading()
                }
                {this.state.status == "unsupported" &&
                  <div className="text-center m-5">
                    <h3>Valitettavasti palvelu ei tue kouluasi tällä hetkellä.</h3>
                    <p>Väärän koulun valitseminen ei myöskään auta, sillä silloin et saa juuri sinun kouluusi liittyvää tietoa.</p>
                    <p>Voit pyytä koulusi lisäystä lähettämällä sähköpostia osoitteeseen <strong>viljami@vilkkuv.fi</strong>.</p>
                  </div>
                }
                {this.state.status == "confirm" &&
                  <SchoolConfirm schools={this.state.schools} no={this.noConfirm} yes={this.selectSchool}/>
                }
                {this.state.status == "multiple" &&
                  <SchoolMultiple schools={this.state.schools} no={this.noConfirm} select={this.selectSchool}/>
                }
              </Suspense>
            </Container>
          </main>
        </div>
      </div>
    );
  }
}

export default SchoolSelect;
