import React, { Component, Suspense } from 'react';

import { Button, Card, CardBody, CardHeader, Col, Row } from 'reactstrap';

class SchoolConfirm extends Component {

  constructor(props){
    super(props)
    this.state = {
      school: props.schools[0]
    }
  }

  render(){
    return (
    <>
      <h3 className="text-center m-5">Opiskeletko koulussa {this.state.school.name}?</h3>
      <Row className="align-items-center" tag="div">
        <Col col="6" sm="6" md="6" xl className="mb-3 mb-xl-0">
          <Button block color="primary" onClick={e => this.props.yes(this.state.school._id)}>Kyllä</Button>
        </Col>
        <Col col="6" sm="6" md="6" xl className="mb-3 mb-xl-0">
          <Button block color="secondary" onClick={this.props.no}>En</Button>
        </Col>
      </Row>
    </>)
  }
}

export default SchoolConfirm;
