import React, { Component, Suspense } from 'react';

import {
  Button,
  Col,
  FormGroup,
  Input,
  Label,
  Row,
} from 'reactstrap';

class SchoolMultiple extends Component {

  constructor(props){
    super(props)
    this.state = {
      schools: this.props.schools.sort((a, b) => a.name.localeCompare(b.name)),
      value: 0
    }
    this.onChange = this.onChange.bind(this)
  }

  onChange(e){
    this.setState({value: e.target.value})
  }

  render(){
    return (
      <div>
        <h3 className="text-center m-5">Valitse koulusi valikosta.</h3>
        <FormGroup>
          <Input type="select" onChange={this.onChange}>
                <option value="0">Valitse tästä</option>
                {
                  this.state.schools.map(function(item, i){
                    return(<option key={item._id} value={item._id}>{item.name}</option>)
                  })
                }
          </Input>
        </FormGroup>

        <Row className="align-items-center" tag="div">
          <Col col="6" sm="6" md="6" xl className="mb-3 mb-xl-0">
            <Button disabled={this.state.value==0} block color="primary" onClick={e => this.props.select(this.state.value)}>Vahvista</Button>
          </Col>
          <Col col="6" sm="6" md="6" xl className="mb-3 mb-xl-0">
            <Button block color="secondary" onClick={this.props.no}>Kouluani ei ole listalla</Button>
          </Col>
        </Row>
      </div>
    )
  }
}

export default SchoolMultiple;
