import React, {Component} from 'react';
import axios from 'axios';
import LoggedIn from './LoggedIn'
import SchoolSelect from './setup/SchoolSelect'
import WilmaSetup from './setup/WilmaSetup'

const CustomHeader = React.lazy(() => import('./Header'));

class App extends Component {

  loading = () => <div className="animated fadeIn pt-1 text-center">Ladataan...</div>

  constructor(props){
    super(props)

    this.state = {
      loadingState: "loading",
      user: {}

    }
    this.getUserData = this.getUserData.bind(this);
  }

  getUserData(){
    axios.get(process.env.REACT_APP_API_URL + '/user/me', { headers: { Authorization: `Bearer ${localStorage.getItem("access_token")}` } })
          .then(res => {
            const result = res.data;
            if(result.success){
              this.setState({user: result, loadingState: "ready"})
            }else if(result.error == "school_not_selected"){
              this.setState({loadingState: "schoolSelect"})
            }else if(result.error == "wilma_url_missing"){
              this.setState({loadingState: "wilmaUrl"})
            }
    })
  }

  componentDidMount(){
    this.getUserData();
  }

  render() {
    if(this.state.loadingState === "loading"){
      return (
        <div className="app">
          <div className="animated fadeIn pt-1 text-center">Ladataan...</div>
        </div>
      );
    }else if(this.state.loadingState === "schoolSelect"){
      return(<div className="app">
        <SchoolSelect refresh={this.getUserData}/>
      </div>)
    }else if(this.state.loadingState === "wilmaUrl"){
      return(<div className="app">
        <WilmaSetup refresh={this.getUserData}/>
      </div>)
    }else if(this.state.loadingState === "ready"){
      return (
        <div className="app">
          <LoggedIn user={this.state.user}/>
        </div>
      );
    }
  }
}

export default App;
