import React, { Component, Suspense } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import axios from 'axios';

import { Container, Input, FormGroup, FormFeedback, Label, FormText, Button, Card, CardBody, CardHeader, Col, Row } from 'reactstrap';


import {
  AppAside,
  AppFooter,
  AppHeader,
  AppSidebar,
  AppSidebarFooter,
  AppSidebarForm,
  AppSidebarHeader,
  AppSidebarMinimizer,
  AppSidebarNav2 as AppSidebarNav,
} from '@coreui/react';

import * as router from 'react-router-dom';

class WilmaSetup extends Component {

  constructor(props){
    super(props)

    this.state = {
      status: "form",
      url: "",
      isValid: false
    }
    this.isValid = this.isValid.bind(this)
    this.submitUrl = this.submitUrl.bind(this)
    this.onChange = this.onChange.bind(this)
  }

  isValid(url){
    return url.includes("/schedule/export/students/") && url.includes("https://");
  }

  onChange(e){
    var url = e.target.value;
    this.setState({url: url, isValid: this.isValid(url), status: "form"})
  }

  submitUrl(){
    this.setState({status: "submitting"})
    axios.post(process.env.REACT_APP_API_URL + '/user/wilma', JSON.stringify({url: this.state.url}), { headers: { Authorization: `Bearer ${localStorage.getItem("access_token")}`, "Content-Type": 'application/json'} })
          .then(res => {
            const result = res.data;
            if(result.success){
              this.props.refresh();
            }else{
              this.setState({status: "form"})
            }
    })
  }

  loading = () => <div className="animated fadeIn pt-1 text-center">Ladataan...</div>

  render(){
    return (
      <div className="app">
        <AppHeader fixed>
          <Suspense  fallback={this.loading()}>
          </Suspense>
        </AppHeader>
        <div className="app-body">
          <main className="main">
            <Container fluid>
              <Suspense fallback={this.loading()}>
                {this.state.status == "form" &&
                  <>
                    <h3 className="text-center m-5">Liitä Wilman työjärjestyksen tilauslinkki alla olevaan kenttään.</h3>
                    <FormGroup row className="justify-content-center">
                      <Col md="9">
                        <Input onChange={this.onChange} type="text" id="text-input" name="text-input" invalid={!this.state.isValid && this.state.url != ""} valid={this.state.isValid} placeholder="Liitä linkki tähän" />
                      </Col>
                    </FormGroup>
                    <Row className="justify-content-center">
                      <Col md="9">
                        <Button block disabled={!this.state.isValid} onClick={this.submitUrl} color="primary">Vahvista</Button>
                      </Col>
                    </Row>
                  </>
                }
                {this.state.status == "submitting" &&
                  <>
                    <h3 className="text-center m-5">Tietojasi ladataan...</h3>
                    <p className="text-center">Tässä voi kestää useita kymmeniä sekunteja.</p>
                    <p className="text-center"><strong>Ethän sulje tätä sivua kesken latauksen.</strong></p>
                    <Row className="justify-content-center">
                      <i className="fa fa-spinner fa-lg fa-spin mt-4"></i>
                    </Row>
                  </>
                }
              </Suspense>
            </Container>
          </main>
        </div>
      </div>
    );
  }
}

export default WilmaSetup;
